.ResultsList table {
    margin: auto;
    margin-top: 40px;
    border: 1px solid black;
    border-collapse: collapse;
    margin-bottom: 80px;
}

.ResultsList table thead tr th {
    padding: 20px 30px;
    font-size: 1.5em;
    font-weight: 400;
    text-transform: uppercase;
}

.ResultsList table tbody tr {
    border: 1px solid black;
    font-size: 1.2em;
}

.ResultsList table tbody> :first-child {
    background-color: rgba(0, 128, 0, 0.863);
}

.ResultsList table tbody> :nth-child(2) {
    background-color: rgba(0, 128, 0, 0.445);
}

.ResultsList table tbody> :nth-child(3) {
    background-color: rgba(0, 128, 0, 0.247);
}

.ResultsList .ResultsMsg .TopNames {
    list-style: none;
    padding-left: 0;
    display: flex;
    flex-wrap: wrap;
    border-radius: 20px;
    min-height: 50px;
    padding-top: 20px;
    justify-content: center;
}

.ResultsList .ResultsMsg .WinnerName {
    height: 60px;
    font-size: 30px;
    background-color: #94CB79;
    letter-spacing: 1px;
    box-shadow: 5px 5px rgba(0, 0, 0, 0.2);
    font-family: 'Sniglet', cursive;
    color: white;
    border: none;
    border-radius: 10px;
}

.ResultsList .ResultsMsg .TopNames li {
    background-color: #94CB79;
    color: white;
    font-family: 'Nunito Sans', sans-serif;
    font-weight: 500;
    padding: 8px 16px;
    margin: 0px 10px;
    margin-bottom: 20px;
    border-radius: 10px;
    font-size: 1.5em;
    letter-spacing: 1px;
    box-shadow: 5px 5px rgba(0, 0, 0, 0.2);
    font-family: 'Sniglet', cursive; 
}

.ResultsList .ResultsMsg .ResultsPhoto {
    height: 200px;
    border-radius: 5px;
}