.NamesList {
    list-style: none;
    padding-left: 0;
    display: flex;
    flex-wrap: wrap;
    border: 2px solid #F3C809;
    border-radius: 20px;
    min-height: 50px;
    padding-top: 20px;

}

.NamesList li {
    border: 1px solid #F3C809;
    font-family: 'Nunito Sans', sans-serif;
    font-weight: 500;
    padding: 8px 16px;
    padding-right: 8px;
    margin: 0px 10px;
    margin-bottom: 20px;
    border-radius: 10px;
    font-size: 0.9em;
    letter-spacing: 1px;
    box-shadow: 5px 5px rgba(0, 0, 0, 0.2);
}

.NamesList li span {
    font-weight: 700;
    font-size: 13px;
    padding-left: 8px;
}

.NamesList li span:hover {
    font-weight: 1000;
    font-size: 14px;
}

.NamesList li:hover {
    background-color: pink;
    cursor: pointer
}
