.NamesComparer {
    height: 200px;
    /* border: 5px solid #0934F3; */
    border-radius: 30px;
    margin: auto;
    margin-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    position: relative;
}

.NamesComparer .NameButton {
    cursor: pointer;
    font-size: 20px;
    padding: 15px 30px;
    font-weight: 400;
    border: 2px solid #F3C809;
    border-radius: 10px;
    background-color: white;
    color: #d6b003;
    font-family: 'Sniglet', cursive;
    min-width: 30%;
    box-shadow: 10px 10px rgba(0, 0, 0, 0.2);
    max-width: 90%;
}

@media (pointer: fine) {
    .NamesComparer .NameButton:hover {
        background-color: #94CB79;
        border: none;
        color: white;
    }
}

.NamesComparer .InactiveBtn {
    font-size: 20px;
    padding: 15px 30px;
    font-weight: 400;
    border: 2px solid #F3C809;
    border-radius: 10px;
    background-color: white;
    color: #d6b003;
    font-family: 'Sniglet', cursive;
    min-width: 30%;
    box-shadow: 10px 10px rgba(0, 0, 0, 0.2);
    max-width: 90%;
}

.NamesComparer .Swords {
    opacity: 0.8;
    height: 20px;
    margin: 0 40px;
}

.NamesComparer .Wrapper {
    position: absolute;
    bottom: 10px;
    width: 60%;
}

@media (max-width:520px) {

    .NamesComparer .NameButton {
        font-size: 16px;
        padding: 15px 30px;
        font-weight: 400;
    }

    .NamesComparer .Swords {
        margin: 0 20px;
    }
}