/* GENERAL */

.App {
  text-align: center;
  font-family: 'Sniglet', cursive;
}

.App section {
  margin: auto;
  padding-top: 100px;
  padding-bottom: 40px;
  position: relative;
}

.App .ContentBox {
  width: 70%;
  display: inline-block;
}

.App h2 {
  margin-top: 25px;
  color: #F3C809;
  text-transform: uppercase;
  font-weight: 400;
}

.App h3 {
  text-align: left;
  font-weight: 400;
}

.App p {
  color: rgb(129, 129, 129);
  font-size: 20px;
}

/* SPEECH BUBBLES */

.App .Bubble {
  position: absolute;
  height: 160px;
  opacity: 0.8;
}

.App .Names-pool .SpeechB1 {
  left: 30px;
  top: 0;
}

.App .Names-pool .SpeechB2 {
  right: 30px;
  top: 0px;
}

.App .Comparison .SpeechB3 {
  left: 30px;
  top: 20px;
}

.App .Comparison .SpeechB4 {

  right: 30px;
  top: 20px;
}

/* HEADER */

.App .Header {
  background-color: #79BDCB;
  color: white;
  font-family: 'Sniglet', cursive;
  font-size: 30px;
  text-align: left;
  padding-top: 30px;
  padding-left: 30px;
  position: relative;
}

.App .Header p {
  color: rgb(255, 255, 255);
  font-size: 22px;
  padding-bottom: 30px;
  margin-top: 0;
  font-weight: 200;
}

.App .Header h1 {
  margin-top: 0;
  font-weight: 1000;
  margin-bottom: 10px;
}

.App .Header .Baby {
  position: absolute;
  top: 15px;
  right: 30px;
  width: 150px;
}

/* SECTION 1. NAMES POOL */

.App .Names-pool .CreateIcon {
  opacity: 0.8;
}

.App .Names-pool p {
  color: #F3C809;
}

.BigIcon {
  height: 100px;
}


/* SECTION 2. DUELS OF NAMES */

.App .Comparison {
  background-color: #79BDCB;
  color: white;
  padding-bottom: 0;
}

.App .Comparison .ContentBox h2 {
  color: white;
}

.App .Comparison .ContentBox p {
  color: white;
}

.App .Comparison .ContentBox .CompareWindow p {
  font-style: italic;
  font-size: 16px;
}

.App .CompareWindow {
  margin-bottom: 60px;
  margin-top: 60px;
}

.App .CompareWindow h3 {
  color: white;
  text-align: center;
  text-transform: uppercase;
}


/* FOOTER */
.App .Footer {
  background-color: #79BDCB;
  color: white;
  /* text-align: left; */
  padding-top: 20px;
  /* padding-left: 1%; */
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

}

.App .Footer p {
  color: white;
  margin: auto;
  padding-bottom: 10px;
  flex-basis: 97%;
}


.App .Footer a {
  font-size: 10px;
  color: white;
  padding: 5px 5px;
  padding-bottom: 0;
  text-decoration: none;
  flex-basis: 14%;
}

.App .Footer .PortfolioLink {
  text-decoration: none;
  color: white;
  font-size: 20px;
}

.App .Footer .ContactP{
  display: inline-block;
  font-size: 14px;
}

/* MEDIA RULES */


@media (min-width:992px) {
  .App .ContentBox {
    max-width: 900px;
  }
}

@media (max-width:768px) {
  .App .Header h1 {
    width: 70%;
  }
  .App .Header p {
    font-size: 20px;
    width: 90%;
  }

  .App .Bubble {
    height: 140px;
  }
}

@media (max-width:520px) {
  .App .Header h1 {
    width: 70%;
    font-size: 50px;
  }

  .App .Header p {
    font-size: 20px;
    width: 60%;
  }

  .App .Header .Baby {
    top: auto;
    bottom: 25px;
    right: 20px;
    width: 90px;
  }

  .App .Bubble {
    display: none;
  }

  .App .ContentBox {
    width: 87%;
  }

  .App .Footer a {
    flex-basis: 23%;
  }
}